/* src/components/Header.css */
.header {
    padding: 1rem 2rem;
    background-color: #333;
    color: white;
}

.header .logo {
    font-size: 1.5rem;
    font-weight: bold;
}

.header nav ul {
    list-style: none;
    display: flex;
    gap: 1rem;
}

.header nav ul li {
    margin: 0;
}

.header nav ul li a {
    color: white;
    text-decoration: none;
}

.header nav ul li a:hover {
    text-decoration: underline;
}
